import * as ActionTypes from './ActionTypes';

export const Usersnames = (state={
    errMess: null,
    usersnames: []
}, action) => {
    switch(action.type){
        case ActionTypes.ADD_USERSNAMES:
            return {...state, errMess:null, usersnames: action.payload};


            
        case ActionTypes.USERSNAMES_FAILED:
            return {...state, errMess:action.payload};
        default :
            return state;
    }
}