
import {Component} from 'react';
import {Label, Col, Row, Button } from 'reactstrap';
import {Control, Errors, Form} from 'react-redux-form';
import { EyeSlash, Eye } from 'react-bootstrap-icons';
import xcvbn from 'zxcvbn';

const required = (val) => val && val.length;
const maxLength = (len) => (val)=> !(val) || (val.length <= len);
const minLength =(len) => (val) => !(val) || (val.length >= len);


export default class Signup extends Component{

    constructor(props) {
        super(props);

        this.state = {
            values:{},
            showPassword: false,
            passwordErrMess: null,
            passwordStrengthIndicator: 0
        }
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleShowPassword = this.handleShowPassword.bind(this);
        this.handlePasswordOnChange = this.handlePasswordOnChange.bind(this);
    }

    handleShowPassword(event){
        event.preventDefault();
        this.setState({
            showPassword: !this.state.showPassword
        });
    }

    handleSubmit(values){
        const password = values.password;
        if(password.length == 0){
            this.setState({
                passwordErrMess: 'Required '
            });
        }
        else if(password.length < 8 ){
            this.setState({
                passwordErrMess: 'Must have length greater of 7 '
            });
        }else if(!/\d/.test(password)){
            this.setState({
                passwordErrMess: 'Must contain a number'
            });
        }else if(!/[`!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?~]/.test(password)){
            this.setState({
                passwordErrMess: 'Must contain a special character'
            });
        }else{
            this.setState({
                values: values
            });
            this.props.signupUser(values.username,  values.password, values.firstname, values.lastname);
            this.props.resetSignupForm();
        }
    }
    handlePasswordOnChange(e){
        this.setState({
            passwordErrMess: null,
            passwordStrengthIndicator : (xcvbn(e.target.value).score * 100 / 4)
        });
    }
    render(){
        const passwordStrengthColor = () => {
            switch(this.state.passwordStrengthIndicator * 4 / 100){
                case 0: return '#828282';
                case 1: return '#EA1111';
                case 2: return '#FFAD00';
                case 3: return '#9bc158';
                case 4: return '#00b500';
                default : return 'none';
            }}
        const changePasswordStrngthColor = () => ({
            width: `${this.state.passwordStrengthIndicator}%`,
             background: passwordStrengthColor(),
             height: '7px'
            });
        const passwordStrengthLabel =()=>{
            switch(this.state.passwordStrengthIndicator * 4 / 100){
                case 0: return 'Very week';
                case 1: return 'Week';
                case 2: return 'Fear';
                case 3: return 'Good';
                case 4: return 'Strong';
                default : return '';
            }
        }
        return (
            <div className="container">
                <div className="row row-container"><div className="col-12 mt-5">
                      <h3>Signup For New Account</h3>
                      <hr />
                   </div>
                <div className="col-12 col-md-9">
                        <Form model='signup' onSubmit={(values) => this.handleSubmit(values)} >
                            <Row className="form-group">
                                <Label htmlFor="username" md={2}>User Name</Label>
                                <Col md={10}>
                                    <Control.text model=".username" id="username" name="username"
                                        placeholder="User Name"
                                        className="form-control"
                                        validators={{required, minLength:minLength(3), maxLength:maxLength(15)
                                        , usernameExists:(val) => ! this.props.usersnames.includes(val)
                                        }}
                                        />
                                        <Errors
                                            className='text-danger'
                                            model='.username'
                                            show='touched'
                                            messages={{
                                                required:'Required ',
                                                minLength: 'Must be greater than 2 characters ',
                                                maxLength: 'Must be less than 15 characters ',
                                                usernameExists: 'username is not available '
                                            }}
                                        />
                                </Col>
                            </Row>
                            <Row className="form-group">
                                <Label htmlFor="password" md={2}>Password</Label>
                                <Col md={10}>
                                    <div>
                                        <p className="password-hint-signup">hint: the password must have the length of 8 and must contain a number and special character</p>
                                    </div>
                                    <div className="signup-password-container">
                                    {
                                        !this.state.showPassword ? 
                                            <div>
                                            <Control.password model=".password" id="password" name="password"
                                            placeholder="Password"
                                            autocomplete="new-password"
                                            className="form-control signup-password-field"
                                            onChange={this.handlePasswordOnChange}
                                            />
                                            <button className='signup-show-password-btn' onClick={this.handleShowPassword}><Eye/></button>
                                            </div>
                                        :
                                        <div>
                                        <Control.text model=".password" id="password" name="password"
                                            placeholder="Password"
                                            className="form-control signup-password-field"
                                            onChange={this.handlePasswordOnChange}
                                            // validators={{required, minLength:minLength(3),
                                            //      maxLength:maxLength(15), mustContainNumber: (val) => ( !val || /\d/.test(val)),
                                            //      mustContainSpecialChar: (val) => (/[`!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?~]/.test(val))}}
                                            /><button className='signup-show-password-btn' onClick={this.handleShowPassword}><EyeSlash/></button>
                                       </div>
                                    }
                                    </div>
                                    <div className='err-validation-password-signup'>
                                        <p>{this.state.passwordErrMess}</p>
                                    </div>
                                    <>
                                        <div className="progress" style={{height: '7px'}}>
                                            <div className="progress-bar progress" style={changePasswordStrngthColor()}>

                                            </div>
                                        </div>
                                        <p className='text-right' style={{color: passwordStrengthColor()}}>{passwordStrengthLabel()}</p>
                                    </>
                                    
                                </Col>
                            </Row>
                            <Row className="form-group">
                                <Label htmlFor="firstname" md={2}>First Name</Label>
                                <Col md={10}>
                                    <Control.text model=".firstname" id="firstname" name="firstname"
                                        placeholder="First Name"
                                        className="form-control"
                                        validators={{required, minLength:minLength(3), maxLength:maxLength(15)}}
                                        />
                                        <Errors
                                            className='text-danger'
                                            model='.firstname'
                                            show='touched'
                                            messages={{
                                                required:'Required ',
                                                minLength: 'Must be greater than 2 characters ',
                                                maxLength: 'Must be less than 15 characters '
                                            }}
                                        />
                                </Col>
                            </Row>
                            <Row className="form-group">
                                <Label htmlFor="lastname" md={2}>Last Name</Label>
                                <Col md={10}>
                                    <Control.text model=".lastname" id="lastname" name="lastname"
                                        placeholder="Last Name"
                                        className="form-control"
                                        validators={{required, minLength:minLength(3), maxLength:maxLength(15)}}
                                         />
                                         <Errors
                                            className='text-danger'
                                            model='.lastname'
                                            show='touched'
                                            messages={{
                                                required:'Required ',
                                                minLength: 'Must be greater than 2 characters ',
                                                maxLength: 'Must be less than 15 characters '
                                            }}
                                        />
                                </Col>
                            </Row>
                            
                            
                            <Row className="form-group">
                                <Col md={{size:10, offset: 2}}>
                                    <Button type="submit" color="primary" className='create-account-buton'>
                                    Create Account
                                    </Button>
                                </Col>
                            </Row>
                        </Form>
                    </div>
            </div>
            </div>
        );
    }
}