import React, { Component } from 'react';
import Rating from '@material-ui/lab/Rating';
import { styled } from '@material-ui/core/styles';
import StarRounded from '@material-ui/icons/StarRounded';
import {Progress} from 'reactstrap';


const StyledRating = styled(Rating)({
    '& .MuiRating-iconFilled': {
      color: '#FFED76',
    },
    '& .MuiRating-iconEmpty': {
      color: '#e0e0d1',
    },
  });

const RatingChart= ({commentsArray})=> {
    const ratingArray = [0,0,0,0,0];
    for(var i = 0 ; i < commentsArray.length ; i++) {
        
        switch(commentsArray[i].rating){
            case 1:
                ratingArray[0] = ratingArray[0] + 1;
                break;
            case 2: 
                ratingArray[1] = ratingArray[1] + 1;
                break;
            case 3: 
                ratingArray[2] = ratingArray[2] + 1;
                break;
            case 4: 
                ratingArray[3] = ratingArray[3] + 1;
                break;
            case 5: 
                ratingArray[4] = ratingArray[4] + 1;
                break;
            default: ;
        }
    }
    var totalRating = 0;
    for (var i = 0; i < ratingArray.length; i++){
        totalRating = totalRating + (i+1) * ratingArray[i];
    }

    const numofRatings = commentsArray.length;
    totalRating = totalRating / numofRatings;
    const fiveStarsPercent = (ratingArray[4] / numofRatings) * 100; 
    const fourStarsPercent = (ratingArray[3] / numofRatings) * 100; 
    const threeStarsPercent = (ratingArray[2] / numofRatings) * 100; 
    const twoStarsPercent = (ratingArray[1] / numofRatings) * 100; 
    const oneStarsPercent = (ratingArray[0] / numofRatings) * 100; 
 
    return (
        <div className='container'>
            <div className='row'>
                <div className='col-12'>
                    <h4 className='align-text-center'>
                        {(totalRating).toFixed(2)}
                    </h4>
                    <div className='align-text-center'>
                        <StyledRating 
                            name="customized-color"
                            defaultValue={totalRating}
                            readOnly={true}
                            precision={0.2}
                            icon={<StarRounded fontSize="inherit" />}
                            emptyIcon={<StarRounded fontSize="inherit" />}
                        />
                    </div>
                </div>
            </div>
            <div className="row">
                        <div className="col-12">
                            <div className="chart-num">5</div>
                            <StarRounded className='chart-star'/>
                            <div className="col-9 progress-chart">
                                <Progress value={fiveStarsPercent}/>
                            </div>
                        </div>
                        <div className="col-12">
                            <div className="chart-num"> 4</div>
                            <StarRounded className='chart-star'/>
                            <div className="col-9 progress-chart">
                                <Progress value={fourStarsPercent}/>
                            </div>
                        </div>
                        <div className="col-12">
                            <div className="chart-num"> 3</div>
                            <StarRounded className='chart-star'/>
                            <div className="col-9 progress-chart">
                                <Progress value={threeStarsPercent}/>
                            </div>
                        </div>
                        <div className="col-12">
                            <div className="chart-num"> 2</div>
                            <StarRounded className='chart-star'/>
                            <div className="col-9 progress-chart">
                                <Progress value={twoStarsPercent}/>
                            </div>
                        </div>
                        <div className="col-12">
                            <div className="chart-num">  1</div>
                            <StarRounded className='chart-star'/>
                            <div className="col-9 progress-chart">
                                <Progress value={oneStarsPercent}/>
                            </div>
                        </div>
            </div>
        </div>
    );
}

export default RatingChart;