import {
    Card, CardImg, CardImgOverlay,
    CardTitle,CardText, Breadcrumb, BreadcrumbItem, Button
  } from 'reactstrap';
import {Link} from 'react-router-dom';
import {Pencil} from 'react-bootstrap-icons';
import CommentForm from './CommentFormComponent';
import {useState} from 'react';
import { Loading } from './LoadingComponent';
import { FadeTransform, Fade, Stagger } from 'react-animation-components';
import { imageUrl } from '../shared/baseUrl';
import {SuitHeartFill , SuitHeart} from 'react-bootstrap-icons';
import RatingChart  from './RatingChartComponent';
import Rating from '@material-ui/lab/Rating';
import { styled } from '@material-ui/core/styles';
import StarRounded from '@material-ui/icons/StarRounded';
import Avatar from '@material-ui/core/Avatar';

const StyledRating = styled(Rating)({
    '& .MuiRating-iconFilled': {
      color: '#FFED76',
    },
    '& .MuiRating-iconEmpty': {
      color: '#e0e0d1',
    },
  });


function RenderProduct({product, favorite, postFavorite, deleteFavorite, isAuthenticated}){
    return(
        <FadeTransform in transformProps={{exitTransform: 'scale(2) translateY(-50%)'}}>
            <div>
                <Card >
                    <CardImg  className='product-details-img' src={ imageUrl + product.image} alt={product.name}/>
                            {
                                isAuthenticated && 
                                <CardImgOverlay>
                                    <Button outline color="primary" onClick={() => favorite ? deleteFavorite(product._id) : postFavorite(product._id)}>
                                        {
                                            favorite ?
                                            <SuitHeartFill/>
                                            : 
                                            <SuitHeart/>
                                        }
                                    </Button>
                                </CardImgOverlay>
                            }
                    
                </Card>
                <div className='mt-5'>
                    <h5>{product.name}</h5>
                </div>
                <hr />
                <div>
                    <p>Price: {(product.price/100).toFixed(2) +''}</p>
                </div>
                <div className='mt-5'>
                    <p>{product.description}</p>
                </div>
            </div>
        </FadeTransform>

            );
}

function RenderComments({commentsList, commentsErrMess}){
    console.log('commentsList' ,commentsList);
    if(commentsErrMess){
        return(<h4 className='error-message'>{commentsErrMess}</h4>);
    }else
    if(commentsList.length > 0){
        const comments = commentsList.map(comment =>{
            
            return(
                    <Fade in>
                        <div  key={comment._id}>
                            <Card className='product-comment-card'>
                            <div className='row'>
                                         <div className='col-1'>
                                             <Avatar  aria-haspopup="true"  variant="contained" 
                                                className="user-avatar-small-screen" alt={comment.author.username} src={ imageUrl + comment.author.image} />
                                     
                                         </div>
                                         <div className='col-11'>
                                         <CardTitle  className='card-title-product-detail'>{comment.author.username}</CardTitle>

                                         </div>
                                     </div>
                                <StyledRating 
                                        name="customized-color"
                                        defaultValue={comment.rating}
                                        readOnly={true}
                                        precision={0.2}
                                        icon={<StarRounded fontSize="inherit" />}
                                        emptyIcon={<StarRounded fontSize="inherit" />}
                                />
                                
                                <CardText>{comment.comment}</CardText>
                                <CardText>
                                    <small className="text-muted">{new Intl.DateTimeFormat('en-US', { year: 'numeric', month: 'short', day: '2-digit'}).format(new Date(Date.parse(comment.createdAt)))}</small>
                                </CardText>
                            </Card>
                        </div>
                    </Fade>                  
            );
        });
        return(
            <>
                <h4>Reviews</h4>
                <hr />
                <Stagger in>
                    {comments}
                </Stagger>
            </>
        );
    }else{
        return(<div></div>);
    }

}

const ProductDetails = ({product,productComments,errMess,isLoading,commentsErrMess,postComment,favorite ,postFavorite, deleteFavorite, isAuthenticated})=>{
    const [isCommentFormModalOpen,setIsCommentFormModalOpen] = useState(false);
    const toggleCommentFormModal =()=>{
        setIsCommentFormModalOpen(!isCommentFormModalOpen);     
    }
    if (isLoading) {
        return(
            <div className="container">
                <div className="row">            
                    <div className="col-12 mt-5">
                        <Loading />
                    </div>
                </div>
            </div>
        );
    }
    else if (errMess) {
        return(
            <div className="container">
                <div className="row">            
                    <h4 className='error-message'>{errMess}</h4>
                </div>
            </div>
        );
    }
    else if (product != null) {
         
        return(
            <div className="container">
                <div className="row">
                    <Breadcrumb className='bread-crumb'>
                        <BreadcrumbItem><Link to='/home'>Home</Link></BreadcrumbItem>
                        <BreadcrumbItem><Link to='/menu'>Menu</Link></BreadcrumbItem>
                        <BreadcrumbItem active>{product.name}</BreadcrumbItem>
                    </Breadcrumb>
                </div>
                <div className="row">
                    <div className="col-12 col-md-4 " >
                        <RenderProduct product={product} favorite={favorite} postFavorite={postFavorite} 
                            deleteFavorite ={deleteFavorite} isAuthenticated={isAuthenticated}/>
                        {productComments.length > 0 && <div> <RatingChart commentsArray={productComments}/> <br /></div>}
                    </div>
                    <div className="col-12 col-md-6 " >
                         { productComments.length > 0 && <RenderComments commentsList={productComments} commentsErrMess={commentsErrMess}/>}
                        { isAuthenticated && <Button className='btn-submit-comment ml-4' outline  color="primary" onClick={toggleCommentFormModal} ><Pencil size="20px"/> Comment</Button>}
                    </div>
                </div>
                <CommentForm toggleModal={toggleCommentFormModal} isModalOpen={isCommentFormModalOpen} productId={product._id} postComment={postComment}/>
            </div>
            
        );
     }
}

export default ProductDetails;