import Menu from './MenuComponent';
import Header from './HeaderComponent';
import Footer from './FooterComponent';
import Home from './HomeComponent';
import Contact from './ContactComponent';
import ProductDetails from './ProductDetailsComponent';
import About from './AboutComponent';
import Favorites from './FavoriteComponent';
import ProfileSettings from './ProfileSettingsComponent';
import Signup from './SignupComponent';

import {Switch, Route, Redirect, withRouter} from 'react-router-dom';
import {connect} from 'react-redux';
import { addComment, postComment, fetchProducts, fetchPromos, fetchComments
        , fetchLeaders, postFeedback, loginUser, logoutUser, updateUser, uploadImage,
         fetchFavorites, postFavorite, deleteFavorite, signupUser, fetchUsersNames } from '../redux/ActionCreators';
import { Component} from 'react';
import {actions} from 'react-redux-form';
import { TransitionGroup, CSSTransition } from 'react-transition-group';


const mapDispatchToProps = dispatch => ({
  
    addComment: (productId, rating, author, comment) => dispatch(addComment(productId, rating, author, comment)),
    fetchProducts: () => { dispatch(fetchProducts())},
    resetFeedbackForm : () =>{dispatch(actions.reset('feedback'))},
    fetchPromos: () => dispatch(fetchPromos()),
    fetchComments: () => dispatch(fetchComments()),
    fetchLeaders: () => dispatch(fetchLeaders()),
    postComment: (productId, rating, author, comment) => dispatch(postComment(productId, rating, author, comment)),
    postFeedback: (firstname, lastname, email, telnum,agree,contactType, message) => dispatch(postFeedback(firstname, lastname, email, telnum,agree,contactType, message)),
    loginUser: (creds) => dispatch(loginUser(creds)),
    logoutUser: () => dispatch(logoutUser()),
    signupUser: (username, password,firstname, lastname) => dispatch(signupUser(username, password,firstname, lastname)),
    fetchFavorites: () => dispatch(fetchFavorites()),
    postFavorite: (productId) => dispatch(postFavorite(productId)),
    deleteFavorite: (productId) => dispatch(deleteFavorite(productId)),
    resetSignupForm : () => dispatch(actions.reset('signup')),
    fetchUsersNames : () => dispatch(fetchUsersNames()),
    resetProfileSettingsForm : () =>dispatch(actions.reset('profileSettings')),
    updateUser : (values) => dispatch(updateUser(values)),
    uploadImage : (image) => dispatch(uploadImage(image))
  });


const mapStateToProps = state =>{
    return {
        products: state.products,
        productsComments: state.productsComments,
        leaders: state.leaders,
        promotions: state.promotions,
        favorites: state.favorites,
        auth: state.auth,
        usersnames: state.usersnames
    }
}

class Main extends Component  {
    constructor(props){
        super(props);
    }
    componentDidMount() {
        this.props.fetchProducts();
        this.props.fetchPromos();
        this.props.fetchComments();
        this.props.fetchLeaders();
        this.props.fetchUsersNames();
      }
      
    render(){
        const ProductWithId = ({match}) => {
            return(
              (this.props.auth.isAuthenticated && this.props.favorites.favorites)
              ?
              <ProductDetails product={this.props.products.products.filter((product) => product._id === match.params.productId)[0]}
                isLoading={this.props.products.isLoading}
                errMess={this.props.products.errMess}
                productComments={ this.props.productsComments ? this.props.productsComments.comments.filter((comment) => comment.product === match.params.productId): null}
                commentsErrMess={  this.props.productsComments ? this.props.productsComments.errMess: false}
                postComment={this.props.postComment}
                favorite={this.props.favorites.favorites.products.some((product) => product._id === match.params.productId)}
                postFavorite={this.props.postFavorite}
                deleteFavorite={this.props.deleteFavorite}
                isAuthenticated={this.props.auth.isAuthenticated}
                />
              :
              <ProductDetails product={this.props.products.products.filter((product) => product._id === match.params.productId)[0]}
                isLoading={this.props.products.isLoading}
                errMess={this.props.products.errMess}
                productComments={ this.props.productsComments ? this.props.productsComments.comments.filter((comment) => comment.product === match.params.productId) : null}
                commentsErrMess={ this.props.productsComments ? this.props.productsComments.errMess : false}
                postComment={this.props.postComment}
                favorite={false}
                postFavorite={this.props.postFavorite}
                deleteFavorite={this.props.deleteFavorite}
                isAuthenticated={this.props.auth.isAuthenticated}
                />
            );
          }

        const PrivateRoute = ({ component: Component, ...rest }) => (
            <Route {...rest} render={(props) => (
              this.props.auth.isAuthenticated
                ? <Component {...props} />
                : <Redirect to={{
                    pathname: '/home',
                    state: { from: props.location }
                  }} />
            )} />
          );
        const RouteForNOtAuthinticated = ({component: Component, ...rest}) =>(
            <Route {...rest} render = {(props) => (
                !this.props.auth.isAuthenticated
                ? <Component {...props} />
                : <Redirect to={{
                    pathname: '/home',
                    state: { from: props.location }
                }}/>
            )}/>
        );
        return(
            <>
                <Header auth={this.props.auth} 
                        loginUser={this.props.loginUser} 
                        logoutUser={this.props.logoutUser} />
                <TransitionGroup>
                    <CSSTransition key={this.props.location.key} classNames='page' timeout={300}>
                        <Switch location={this.props.location}>
                            <Route path='/home' component={
                                ()=> <Home product={this.props.products.products.filter(product => product.featured)[0]}
                                productsLoading={this.props.products.isLoading}
                                productsErrMess={this.props.products.errMess}
                                promotion={this.props.promotions.promotions.filter(promotion => promotion.featured)[0]} 
                                promoLoading={this.props.promotions.isLoading}
                                promoErrMess={this.props.promotions.errMess}
                                leaderLoading={this.props.leaders.isLoading}
                                leaderErrMess={this.props.leaders.errMess}
                                leader={this.props.leaders.leaders.filter(leader => leader.featured)[0]}/>}/>
                            <Route path="/menu/:productId" component={ProductWithId}/>
                            <Route path='/menu' component={()=><Menu products={this.props.products} productsComments={this.props.productsComments} />}/>
                            <Route path='/contactus' component={()=><Contact 
                            resetFeedbackForm={this.props.resetFeedbackForm}
                            postFeedback={this.props.postFeedback}
                            isAuthenticated={this.props.auth.isAuthenticated}
                            />}/>
                            <Route path='/aboutus' component={()=><About leaders={this.props.leaders.leaders} 
                                isLoading={this.props.leaders.isLoading}
                                errMess={this.props.leaders.errMess}/>}/>
                                <RouteForNOtAuthinticated exact path='/signup' component={()=>
                                <Signup  resetSignupForm={this.props.resetSignupForm} 
                                    signupUser={this.props.signupUser} usersnames = {this.props.usersnames.usersnames}/>} />
                            <PrivateRoute exact path="/favorites" 
                                component={() =><Favorites favorites={this.props.favorites} deleteFavorite={this.props.deleteFavorite} />} /> 
                            <PrivateRoute exact path="/profile-settings" 
                                component={() =><ProfileSettings auth={this.props.auth} 
                                                uploadImage = {this.props.uploadImage}
                                                resetProfileSettingsForm ={this.props.resetProfileSettingsForm}
                                                usersnames = {this.props.usersnames.usersnames}
                                                updateUser= {this.props.updateUser}
                                                />} /> 
                            <Redirect to='/home'/> 
                        </Switch>          
                    
                    </CSSTransition>
                </TransitionGroup>
                <Footer isAuthenticated={this.props.auth.isAuthenticated}/>
            </>
        );
    }
}

export default withRouter(connect(mapStateToProps,mapDispatchToProps)(Main));