export const requestSignup = (values) => {
    return {
        type: ActionTypes.SIGNUP_REQUEST,
        values
    }
}
  
export const receiveSignup = (response) => {
    return {
        type: ActionTypes.SIGNUP_SUCCESS,

    }
}
  
export const signupError = (message) => {
    return {
        type: ActionTypes.SIGNUP_FAILURE,
        message
    }
}

export const signupUser = (username, password,firstname, lastname) => (dispatch) => {
    const values = {username, password,firstname, lastname}

    // We dispatch requestLogin to kickoff the call to the API
    dispatch(requestSignup(values))
    return fetch(baseUrl + 'users/signup', {
        method: 'POST',
        headers: { 
            'Content-Type':'application/json' 
        },
        body: JSON.stringify(values)
    })
    .then(response => {
        if (response.ok) {
            return response;
        } else {
            var error = new Error('Error ' + response.status + ': ' + response.statusText);
            error.response = response;
            throw error;
        }
        },
        error => {
            throw error;
        })
    .then(response => response.json())
    .then(response => {
        if (response.success) {
            // If signup was successful, login
            // Dispatch the success action
            dispatch(loginUser({username: values.username, password: values.password}));
            dispatch(receiveSignup());
        }
        else {
            var error = new Error('Error ' + response.status);
            error.response = response;
            throw error;
        }
    })
    .catch(error => dispatch(signupError(error.message)))
};



