export const uploadImage = (image) => (dispatch) => {

    const bearer = 'Bearer ' + localStorage.getItem('token');

    // We dispatch requestLogin to kickoff the call to the API
    dispatch(requestUploadImage())
    return fetch(baseUrl + 'imageUpload', {
        method: 'POST',
        headers: { 
            'Authorization': bearer

        },
        body:  image
    })
    .then(response => {
        if (response.ok) {
            return response;
        } else {
            var error = new Error('Error ' + response.status + ': ' + response.statusText);
            error.response = response;
            throw error;
        }
        },
        error => {
            throw error;
        })
    .then(response => response.json())
    .then(response => {

        if (response.success) {
            // If signup was successful, login
            // Dispatch the success action
            dispatch(updateUser({image: 'assets/images/upload/usersImages/' + response.filename}));
            dispatch(receiveUploadImage());
        }
        else {
            var error = new Error('Error ' + response.status);
            error.response = response;
            throw error;
        }
    })
    .catch(error => dispatch(uploadImageError(error.message)))
};


