export const requestUpdateUser = (values) => {
    return {
        type: ActionTypes.UPDATEUSER_REQUEST,
        values
    }
}
  
export const receiveUpdatedUser= (response) => {
    const creds = {password: JSON.parse(localStorage.getItem('creds')).password ,username: response.username};
    return {
        type: ActionTypes.UPDATEUSER_SUCCESS,
        userFirstname : response.firstname,
        userLastname : response.lastname,
        userImage : response.image,
        user: creds
    }
}
  
export const updateUserError = (message) => {
    return {
        type: ActionTypes.UPDATEUSER_FAILURE,
        message
    }
}

export const updateUser = (values) => (dispatch) => {
    
    dispatch(requestUpdateUser(values))
    const bearer = 'Bearer ' + localStorage.getItem('token');
    return fetch(baseUrl + 'users/' + localStorage.getItem('userId'), {
        method: 'PUT',
        headers: { 
            'Content-Type':'application/json' ,
            'Authorization': bearer
        },
        body: JSON.stringify(values)
    })
    .then(response => {
        if (response.ok) {
            return response;
        } else {
            var error = new Error('Error ' + response.status + ': ' + response.statusText);
            error.response = response;
            throw error;
        }
        },
        error => {
            throw error;
        })
    .then(response => response.json())
    .then(response => {

            localStorage.setItem('userFirstname', response.firstname);
            localStorage.setItem('userLastname', response.lastname);
            localStorage.setItem('userImage', response.image);
            const creds = {password: JSON.parse(localStorage.getItem('creds')).password ,username: response.username};
            localStorage.setItem('creds',JSON.stringify(creds));
            dispatch(receiveUpdatedUser(response));
        
        
    })
    .catch(error => dispatch(updateUserError(error.message)))
};
