import * as ActionTypes from './ActionTypes';

// The auth reducer. The starting state sets authentication
// based on a token being in local storage. In a real app,
// we would also want a util to check if the token is expired.
export const Auth = (state = {
        isLoading: false,
        isAuthenticated: localStorage.getItem('token') ? true : false,
        token: localStorage.getItem('token'),
        user: localStorage.getItem('creds') ? JSON.parse(localStorage.getItem('creds')) : null,
        errMess: null,
        userFirstname: localStorage.getItem('userFirstname'),
        userLastname: localStorage.getItem('userLastname'),
        userImage : localStorage.getItem('userImage'),
        userId: localStorage.getItem('userId')
    }, action) => {
    switch (action.type) {
        case ActionTypes.LOGIN_REQUEST:
            return {...state,
                isLoading: true,
                isAuthenticated: false,
                user: action.creds
            };
        case ActionTypes.LOGIN_SUCCESS:
            return {...state,
                isLoading: false,
                isAuthenticated: true,
                errMess: '',
                token: action.token,
                userFirstname: action.userFirstname,
                userLastname: action.userLastname,
                userImage :action.userImage,
                userId :action.userId
            };
        case ActionTypes.LOGIN_FAILURE:
            return {...state,
                isLoading: false,
                isAuthenticated: false,
                errMess: action.message
            };
        case ActionTypes.LOGOUT_REQUEST:
            return {...state,
                isLoading: true,
                isAuthenticated: true
            };
        case ActionTypes.LOGOUT_SUCCESS:
            return {...state,
                isLoading: false,
                isAuthenticated: false,
                token: '',
                user: null,
                userFirstname:'',
                userLastname: '',
                userImage : '',
                userId: ''
            };
            case ActionTypes.SIGNUP_REQUEST:
                return {...state,
                    isLoading: true,
                };
            case ActionTypes.SIGNUP_SUCCESS:
                return {...state,
                    isLoading: false,
                    errMess: '',
                };
            case ActionTypes.SIGNUP_FAILURE:
                return {...state,
                    isLoading: false,
                    isAuthenticated: false,
                    errMess: action.message
                };
                case ActionTypes.UPLOAD_IMAGE_REQUEST:
                return {...state,
                    isLoading: true,
                };
            case ActionTypes.UPLOAD_IMAGE_SUCCESS:
                return {...state,
                    isLoading: false,
                    errMess: '',
                };
            case ActionTypes.UPLOAD_IMAGE_FAILURE:
                return {...state,
                    isLoading: false,
                    errMess: action.message
                };
            case ActionTypes.UPDATEUSER_REQUEST:
                return {...state,
                    isLoading: true,
                };
            case ActionTypes.UPDATEUSER_SUCCESS:
                return {...state,
                        isLoading: false,
                        errMess: '',
                        userFirstname: action.userFirstname,
                        userLastname: action.userLastname,
                        userImage :action.userImage,
                        user: action.user
                };
            case ActionTypes.UPDATEUSER_FAILURE:
                return {...state,
                    isLoading: false,
                    errMess: action.message
                };
        default:
            return state
    }
}