import {Navbar, NavbarBrand, Jumbotron, NavItem, Nav, Collapse, NavbarToggler, Button , Input , Label , Modal, ModalBody, ModalHeader, Form, FormGroup} from 'reactstrap';
import {NavLink} from 'react-router-dom';
import {Component} from 'react';
import { BoxArrowInRight , BoxArrowRight } from 'react-bootstrap-icons';
import Avatar from '@material-ui/core/Avatar';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem'
import { withStyles } from '@material-ui/core/styles';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import SettingsIcon from '@material-ui/icons/Settings';
import {Link} from 'react-router-dom';
import React from 'react';
import {imageUrl} from '../shared/baseUrl'

const StyledMenu = withStyles({
    paper: {
      border: '1px solid #d3d4d5',
    },
  })((props) => (
    <Menu
      elevation={0}
      getContentAnchorEl={null}
      anchorOrigin={{
        vertical: 'bottom',
        horizontal: 'center',
      }}
      transformOrigin={{
        vertical: 'top',
        horizontal: 'center',
      }}
      {...props}
    />
  ));
  const StyledMenuItem = withStyles((theme) => ({
    root: {
      '&:focus': {
        backgroundColor: theme.palette.primary.main,
        '& .MuiListItemIcon-root, & .MuiListItemText-primary': {
          color: theme.palette.common.white,
        },
      },
    },
  }))((props) => (<MenuItem {...props}/>));


export default class Header extends Component{
    constructor(props){
        super(props);
        
        this.state = {
            isNavOpen: false,
            isModalOpen: false,
            anchorEl: null
        };
        this.toggleNav = this.toggleNav.bind(this);
        this.toggleModal = this.toggleModal.bind(this);
        this.handleLogin = this.handleLogin.bind(this);
        this.handleLogout = this.handleLogout.bind(this);
        this.closeNav = this.closeNav.bind(this);
        this.handleClick = this.handleClick.bind(this);
        this.handleClose = this.handleClose.bind(this);
        this.handleProfileSettings = this.handleProfileSettings.bind(this);
        this.handleSignout = this.handleSignout.bind(this);
    }
   
    
    toggleNav = ()=>{
        this.setState({
            isNavOpen: !this.state.isNavOpen
          });
          setTimeout(()=>{console.log('toggleModal', this.state)},3000)
    }
    toggleModal() {
        this.setState({
          isModalOpen: !this.state.isModalOpen
        });
        setTimeout(()=>{console.log('toggleModal', this.state)},3000)
      }
     handleLogin =(event)=> {
        this.toggleModal();
        this.props.loginUser({username: this.username.value, password: this.password.value});
        event.preventDefault();

    }
    
    handleLogout() {
        this.props.logoutUser();
    }

    closeNav(){
        if(this.state.isNavOpen)
        this.setState({
            isNavOpen: !this.state.isNavOpen
          });     
    }
    
    handleClick = (event) => {
        this.setState({
            anchorEl: event.currentTarget
        }); 
      };

      handleClose = () => {
        this.setState({
            anchorEl: null
        }); 
      };

      handleProfileSettings = () =>{
        this.handleClose();
      }

      handleSignout = () => {
        this.handleLogout();
        this.handleClose();

      }

    render() {
        return(
            <> 
                <Navbar dark expand='md' className='navbar'>
                    <div className="container">
                        <NavbarToggler onClick={this.toggleNav}/>
                        {
                            this.props.auth.isAuthenticated ?
                                <NavbarBrand className="d-none d-md-block" href="/"><img src="/easymarket1/logo192.png" width='50px' height='50px' alt="easy market logo" /></NavbarBrand>
                            :
                                <NavbarBrand href="/"><img src="/easymarket1/logo192.png" width='50px' height='50px' alt="easy market logo" /></NavbarBrand>

                            
                        }
                        {   this.props.auth.isAuthenticated &&
                            <Avatar  aria-haspopup="true"  variant="contained" className="d-md-none"
                                     className="d-block d-md-none user-avatar-small-screen" alt={this.props.auth.userFirstname} src={ imageUrl + this.props.auth.userImage} />
                                     }
                        <Collapse isOpen={this.state.isNavOpen} navbar>
                            <Nav navbar>
                                <NavItem className='m-2' onClick={this.closeNav}>
                                    <NavLink className='nav-link' to="/home">Home</NavLink>
                                </NavItem>
                                <NavItem className='m-2' onClick={this.closeNav}>
                                    <NavLink className='nav-link' to="/menu">Menu</NavLink>
                                </NavItem>
                                <NavItem className='m-2' onClick={this.closeNav}>
                                    <NavLink className='nav-link' to="/aboutus">About</NavLink>
                                </NavItem>
                                <NavItem className='m-2' onClick={this.closeNav}>
                                    <NavLink className='nav-link' to="/contactus">Contact</NavLink>
                                </NavItem>
                                { this.props.auth.isAuthenticated &&
                                <NavItem className='m-2' onClick={this.closeNav}>
                                    <NavLink className='nav-link' to="/favorites">Favorites</NavLink>
                                </NavItem>
                                }
                                { this.props.auth.isAuthenticated &&
                                <NavItem className='d-block d-md-none  m-2' onClick={this.closeNav}>
                                    <NavLink className='nav-link' to="/profile-settings">Profile Settings</NavLink>
                                </NavItem>
                                }
                                { this.props.auth.isAuthenticated &&
                                <NavItem className='d-block d-md-none  m-2' onClick={this.handleSignout}>
                                    <NavLink className='nav-link' to='/' onClick={this.closeNav}>Signout</NavLink>
                                </NavItem>
                                }
                                
                            </Nav>
                            
                        </Collapse>                        
                    </div>  
                    <Nav className="ml-auto" navbar>
                        <NavItem>
                            {
                                !this.props.auth.isAuthenticated ?
                                <Button   onClick={this.toggleModal} color="link" className='btn-login'><BoxArrowInRight/> Login</Button>
                                :
                                <div className="d-none d-md-block">
                                    <Avatar aria-controls="customized-menu" aria-haspopup="true" onClick={this.handleClick} variant="contained"
                                     className="user-avatar" alt={this.props.auth.userFirstname} src={ imageUrl + this.props.auth.userImage} />
                                    <StyledMenu
                                            id="customized-menu"
                                            anchorEl={this.state.anchorEl}
                                            keepMounted
                                            open={Boolean(this.state.anchorEl)}
                                            onClose = {this.handleClose} 
                                        > 
                                            <StyledMenuItem  onClick={this.handleProfileSettings}>
                                                <ListItemIcon>
                                                    <SettingsIcon fontSize="small"  />
                                                </ListItemIcon>
                                                <Link to="/profile-settings" class="profile-settings-text-dropdown-menu"><ListItemText primary=" Profile Settings" /></Link>
                                            </StyledMenuItem>
                                            <StyledMenuItem onClick={this.handleSignout}  >
                                                <ListItemIcon >
                                                    <BoxArrowRight size="18px" />
                                                </ListItemIcon>
                                                <ListItemText primary="Signout"  />
                                            </StyledMenuItem>
                                        </StyledMenu>
                                    </div>
                            }
                        </NavItem>
                    </Nav> 
                </Navbar>
                <Jumbotron>
                    <div className="container">
                        <div className="row row-header">
                            <div className="col-12 col-sm-6">
                                <h1 className='header-title'>Easy Market</h1>
                                <p className='header-subtitle'>We take inspiration from the World's best markets, and create a unique  experience. Hope you enjoy the experience.</p>
                            </div>
                            <div className="d-none d-md-block col-md-6 jumptron-logo-img">
                                <img src="/easymarket1/logo512.png" alt="logo" width="200px" />
                            </div>
                        </div>
                    </div>
                </Jumbotron>
                <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                    <ModalHeader toggle={this.toggleModal}>Login</ModalHeader>
                    <ModalBody>
                        <Form onSubmit={this.handleLogin}>
                                <FormGroup>
                                    <Label htmlFor="username">Username</Label>
                                    <Input type="text" id="username" name="username"
                                        innerRef={(input) => this.username = input} />
                                </FormGroup>
                                <FormGroup>
                                    <Label htmlFor="password">Password</Label>
                                    <Input type="password" id="password" name="password"
                                        innerRef={(input) => this.password = input}  />
                                </FormGroup>
                               
                                <br />
                                <div className="row">
                                    <div className="col-6 mt-2">
                                        <Link to="/signup" onClick={this.toggleModal}>Create Account</Link>
                                    </div>
                                    <div className="col-6">
                                        <Button type="submit" value="submit" color="primary" className='login-button'>Login</Button>
                                    </div>
                                </div>
                            </Form>
                    </ModalBody>
                </Modal>   
            </>
        );
    }
}