import {Component} from 'react';
import {imageUrl} from '../shared/baseUrl'
import { Label, Col, Row, Button } from 'reactstrap';
import {Control, Errors, Form} from 'react-redux-form';

const required = (val) => val && val.length;
const maxLength = (len) => (val)=> !(val) || (val.length <= len);
const minLength =(len) => (val) => !(val) || (val.length >= len);

 export default class ProfileSettings extends Component  {
    constructor(props) {
        super(props);

        this.state ={
            editUserName : false,
            editFirstName : false,
            editLastName : false,
            setNewPassword : false,
            passwordIsTrue: false,
            passwordNotMatchedErrMess: ''
        }
        this.handleEditUserName = this.handleEditUserName.bind(this);
        this.handleEditFirstName = this.handleEditFirstName.bind(this);
        this.handleEditLastName = this.handleEditLastName.bind(this);
        this.handleSetNewPassword = this.handleSetNewPassword.bind(this);


    }
    imageHandler =(e) => {
                let uploadImage =  e.target.files[0];
                let form = new FormData()
                form.append('imageFile',uploadImage)

                this.props.uploadImage(form);

    }

        handleSubmit (values){
            if(this.state.editUserName){
                this.props.updateUser({username: values.username});
                this.setState({editUserName: false});
            }else if(this.state.editFirstName){
                this.props.updateUser({firstname: values.firstname});
                this.setState({editFirstName: false});
            }else if(this.state.editLastName){
                this.props.updateUser({lastname: values.lastname});
                this.setState({editLastName: false});
            }else
            if(this.state.passwordIsTrue && this.state.setNewPassword){
                if(values.newPassword === values.confirmPassword){
                    console.log('Change Password here');
                    this.setState({passwordIsTrue: false, setNewPassword:false});
                }else {
                    this.setState({
                        passwordNotMatchedErrMess: 'Doesn\'t match ...'
                    });
                }
            }else
            if(this.state.setNewPassword){
                if(values.password === this.props.auth.user.password){
                    this.setState({
                        passwordIsTrue: true,
                    });
                }else{
                    alert('Password is Wrong !');
                }
            }
           

            this.props.resetProfileSettingsForm();
           
        }

        handleEditUserName(){
            this.setState({
                editUserName : true,
                editFirstName : false,
                editLastName : false,
                setNewPassword: false,
                passwordIsTrue: false
            });
        }

        handleEditFirstName(){
            this.setState({
                editFirstName : true,
                editLastName : false,
                editUserName : false,
                setNewPassword: false,
                passwordIsTrue: false
            });
        }

        handleEditLastName(){
            this.setState({
                editLastName : true,
                editUserName : false,
                editFirstName : false,
                setNewPassword: false,
                passwordIsTrue: false
            });
        }

        handleSetNewPassword(){
            this.setState({
                editLastName : false,
                editUserName : false,
                editFirstName : false,
                setNewPassword: true,
                passwordIsTrue: false
            });
        }
        render()
        {
            return (
            <div className="container">
                <div className="row">
                    <div className="col-12 ">
                        
                            <img  class="imag-center"
                                        alt={this.props.auth.userFirstname}
                                         src={ imageUrl + this.props.auth.userImage} />
                    
                    </div>
                    <div className='col-12 text-center mt-5'>
                        <Label for='imageFile' className=' change-photo-btn btn' >Change Photo</Label>
                        
                    </div>
                    <input type='file' name='imageFile'  style={{"visibility":"hidden"}}
                        id='imageFile' accept ='image/*' onChange={this.imageHandler}/>
                       
                </div>
                <div className="row">
                    <div className="col-12 profile-settings-form-container">
                        <div className="col-12 col-md-8 offset-md-1 mt-5">
                                <Form model='profileSettings' onSubmit={(values) => this.handleSubmit(values)} autocomplete="new-password">
                                    <Row className="form-group">
                                        <Label htmlFor="username" md={3}>Username :</Label>
                                        <Col md={9}>
                                        { this.state.editUserName ?
                                                <Row>
                                                    <Col md={8} className='mt-1'>
                                                            <Control.text model=".username" id="username" name="username"
                                                                placeholder="Username"
                                                                className="form-control"
                                                                validators={{required, minLength:minLength(3),
                                                                 maxLength:maxLength(15),
                                                                 usernameExists:(val) => ! this.props.usersnames.includes(val)
                                                                 }}
                                                                />
                                                                <Errors
                                                                    className='text-danger text-left'
                                                                    model='.username'
                                                                    show='touched'
                                                                    messages={{
                                                                        required:'Required',
                                                                        minLength: 'Must be greater than 2 characters',
                                                                        maxLength: 'Must be less than 15 characters',
                                                                        usernameExists: 'username is not available'
                                                                    }}
                                                                />
                                                    </Col>
                                                    <Col md={4}>
                                                        <Button type="submit" className='profile-settings-save-btn' color="primary" size='sm'>
                                                            Save
                                                        </Button>
                                                    </Col>
                                                </Row>
                                                :
                                                <div className="row mt-2">
                                                        <div className="col-6 text-left"><Label className='text-left'>{this.props.auth.user.username}</Label></div>
                                                      <div className="col-4 offset-2 text-right profile-settings-edit-link"><a className='text-right ' onClick={this.handleEditUserName}>Edit Username</a></div>
                                                
                                                </div>
                                                }
                                        </Col>
                                    </Row>
                                    <Row className="form-group">
                                        <Label htmlFor="firstname" md={3}>First Name :</Label>
                                        <Col md={9}>
                                                {   this.state.editFirstName ?
                                                        
                                                        <Row>
                                                            <Col md={8} className='mt-1'>
                                                            <Control.text model=".firstname" id="firstname" name="firstname"
                                                                placeholder="First Name"
                                                                className="form-control"
                                                                validators={{required, minLength:minLength(3), maxLength:maxLength(15)}}
                                                                />
                                                                <Errors
                                                                    className='text-danger text-left'
                                                                    model='.firstname'
                                                                    show='touched'
                                                                    messages={{
                                                                        required:'Required',
                                                                        minLength: 'Must be greater than 2 characters',
                                                                        maxLength: 'Must be less than 15 characters'
                                                                    }}
                                                                />
                                                                </Col>
                                                                <Col md={4}>
                                                                    <Button type="submit" className='profile-settings-save-btn' color="primary" size='sm'>
                                                                        Save
                                                                    </Button>
                                                                </Col>
                                                                </Row>
                                                           
                                                    :
                                                    <div className="row mt-2">
                                                        <div className="col-6 text-left"><Label className='text-left'>{this.props.auth.userFirstname}</Label></div>
                                                      <div className="col-4 offset-2 text-right profile-settings-edit-link"><a className='text-right ' onClick={this.handleEditFirstName}>Edit Firstname</a></div>
                                                    </div>
                                                }
                                        </Col>
                                    </Row>
                                    <Row className="form-group">
                                        <Label htmlFor="lastname" md={3}>Last Name :</Label>
                                        <Col md={9}>
                                            {       
                                                this.state.editLastName ? 
                                                    <Row>
                                                        <Col md={8} className='mt-1'>
                                                            <Control.text model=".lastname" id="lastname" name="lastname"
                                                                placeholder="Last Name"
                                                                className="form-control"
                                                                validators={{required, minLength:minLength(3), maxLength:maxLength(15)}}
                                                                />
                                                                <Errors
                                                                    className='text-danger text-left'
                                                                    model='.lastname'
                                                                    show='touched'
                                                                    messages={{
                                                                        required:'Required',
                                                                        minLength: 'Must be greater than 2 characters',
                                                                        maxLength: 'Must be less than 15 characters'
                                                                    }}
                                                                />
                                                        </Col>
                                                        <Col md={4}>
                                                            <Button type="submit" className='profile-settings-save-btn' color="primary" size='sm'>
                                                                Save
                                                            </Button>
                                                        </Col>
                                                    </Row>
                                                :
                                                    <div className="row mt-2 ">
                                                        <div className="col-6 text-left"><Label className='text-left'>{this.props.auth.userLastname}</Label></div>
                                                      <div className="col-4 offset-2 text-right profile-settings-edit-link"><a className='text-right ' onClick={this.handleEditLastName}>Edit Lastname</a></div>
                                                    </div>
                                            }        
                                        </Col>
                                    </Row>
                                    {/* <Row className="form-group">
                                        {
                                            this.state.setNewPassword ?
                                                
                                                    <>
                                                        {
                                                            this.state.passwordIsTrue ?
                                                            <><Col md={12}>
                                                                <Row className="form-group">
                                                                    <Label htmlFor="newPassword" md={3}>New Password :</Label>
                                                                    <Col md={7}>
                                                                        <Control.password model=".newPassword" id="newPassword" name="newPassword"
                                                                            placeholder="New Password"
                                                                            className="form-control"
                                                                             validators={{required, minLength:minLength(8),
                                                                                 maxLength:maxLength(15), mustContainNumber: (val) => ( !val || /\d/.test(val)),
                                                                                 mustContainSpecialChar: (val) => (/[`!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?~]/.test(val))}}
                                            
                                                                            />
                                                                            <Errors
                                                                            className="text-danger text-left"
                                                                            model=".newPassword"
                                                                            show="touched"
                                                                            messages={{
                                                                                required: 'Required ',
                                                                                minLength: 'Must have length greater of 7 ',
                                                                                maxLength: 'Must be 15 numbers or less ',
                                                                                mustContainNumber: ' Must contain number ',
                                                                                mustContainSpecialChar: 'Must contain a special character'
                                                                            }}
                                                                        />
                                                                    </Col>
                                                                
                                                                </Row>
                                                                </Col>
                                                                <Col md={12}>
                                                                <Row className="form-group">
                                                                <Label htmlFor="confirmPassword" md={3}>Confirm Password :</Label>
                                                                    <Col md={7}>
                                                                        <Control.password model=".confirmPassword" id="confirmPassword" name="confirmPassword"
                                                                            placeholder="Confirm Password"
                                                                            className="form-control"
                                                                            />
                                                                           
                                                                    </Col>
                                                                    <Col md={2}>
                                                                        <Button type="submit" className='profile-settings-save-btn' color="primary" size='sm'>
                                                                            Save
                                                                        </Button>
                                                                    </Col>
                                                                </Row>
                                                                <div className='err-validation-password-signup text-left offset-md-3'>
                                                                    <p>{this.state.passwordNotMatchedErrMess}</p>
                                                                </div>
                                                                </Col>
                                                            </>
                                                            :
                                                            <>
                                                            <Label htmlFor="password" md={3}>Enter Password</Label>
                                                            <Col md={7} className='mt-1'>
                                                                <Control.password model=".password" id="password" name="password"
                                                                    placeholder="Password"
                                                                    className="form-control"
                                                                   />
                                                            </Col>
                                                            <Col md={2}>
                                                        
                                                                <Button type="submit" className='profile-settings-save-btn' color="primary" size='sm' >
                                                                    Ok
                                                                </Button>
                                                            </Col>
                                                        </>
                                                        }
                                                    </>
                                                
                                            :
                                                <div className="profile-settings-set-new-password-link"><a onClick={this.handleSetNewPassword}>Set New Password</a></div>
                                        }
                                    </Row>
                                 */}
                                </Form>
                            </div>
                        </div>
                    </div>
            </div>
        );}
    
}
