import {createStore, combineReducers, applyMiddleware } from 'redux';
import {Products} from './products';
import {Leaders} from './leaders';
import {Promotions} from './promotions';
import {Comments} from './comments';
import { favorites } from './favorites';
import { Auth } from './auth';
import {Usersnames} from './usersnames';
import {InitialFeedback} from './forms';
import { InitialSignup } from './forms';
import { InitialProfileSettings } from './forms';

import thunk from 'redux-thunk';
import logger from 'redux-logger';
import {createForms} from 'react-redux-form';

export const ConfigureStore = () =>{
    const store = createStore(
        combineReducers({
            products:Products,
            promotions:Promotions,
            leaders: Leaders,
            productsComments: Comments,
            auth: Auth,
            favorites,
            usersnames: Usersnames,
            ...createForms({feedback: InitialFeedback, signup:InitialSignup,profileSettings: InitialProfileSettings})
        }),
        applyMiddleware(thunk, logger)
    );
    return store;
}